import React from 'react'

const UserTable = props => {
	return (
		<table>
			<thead>
				<tr>
					<th>Name</th>
					<th>Username</th>
					<th>Actions</th>
				</tr>
			</thead>
			<tbody>
				{props.users.length > 0 ? (
					props.users.map(user => (
						<tr key={user.name}>
							<td>{user.name}</td>
							<td>{user.username}</td>
							<td>
								<button
									onClick={() => {
										console.log(user)
										props.editRow(user)
									}
									}
									className="button muted-button"
								>
									Edit
								</button>
								<button
									onClick={() => props.deleteUser(user.name)}
									className="button muted-button"
								>
									Delete
								</button>
							</td>
						</tr>
					))
				) : (
					<tr>
						<td colSpan={3}>No users</td>
					</tr>
				)}
			</tbody>
		</table>
	)
}

export default UserTable
